import React, { useState, useEffect, lazy, Suspense } from "react";
import { Box, ToggleButton, ToggleButtonGroup } from '@mui/material';
import HeaderSection from "../components/HeaderSection";
import ScrollProgressBar from '../components/ScrollProgressBar';
import FloatingButton from "../components/FloatingButton";

import SocialInfrastructureTable from "../components/SocialInfrastructureTable";
import SIOverallIndicatorMap from "../components/SocialInfrastructureMaps/SIOverallIndicatorMap";
import SISubIndicatorMap from "../components/SocialInfrastructureMaps/SISubIndicatorMap";
import IndicatorSelect from "../components/IndicatorSelect";
import GlossaryTerm from "../components/GlossaryTerm";
import OutlookChart from "../components/OutlookChart";

import "../styles/HomePage.css";
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import TrainIcon from '@mui/icons-material/Train';

import BackgroundSection from "../components/sections/BackgroundSection";
import IncomeSection from "../components/sections/IncomeSection";
// import ResidualIncomeSection from "../components/sections/ResidualIncomeSection";

const ResidualIncomeSection = lazy(() => import("../components/sections/ResidualIncomeSection"));


export default function HomePage() {

    const [showFloatingButton, setShowFloatingButton] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
          setShowFloatingButton(window.scrollY > 1400);
        };
    
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
      }, []);

    const [selectedIndicator, setSelectedIndicator] = useState(1);

    const handleIndicatorChange = (newValue) => {
        setSelectedIndicator(newValue);
    };


    const [siMode, setSiMode] = useState("so_cars");
    const [siSubMode, setSiSubMode] = useState("so_sub");

    const handleToggleModeChange = (type, newValue) => {
        if (newValue !== null) {
            if (type === "si") {
                setSiMode(newValue);
            } else if (type === "si-sub") {
                setSiSubMode(newValue);
            }
        }
    };

    return (
        <>

        <HeaderSection />

        <div className='title-wrapper'>
            <section className="title-section" id="intro">
            
            <h1 className="title">Perspektiven auf Ungleichheit in sterreich</h1>
            <h3>Residualeinkommen, soziale Infrastruktur und Zeitverwendung</h3>

            <div className='image-container logo-large'>
                <img style={{width: '240px'}} src={`${process.env.PUBLIC_URL}/images/Logo_project_icons.png`} alt="showing the four core aspects of the project" />
            </div>
            </section>
        </div>


        {/* Scrollable Content */}
        <div className="content-wrapper">

            <section className='background' id="background">
                <BackgroundSection/>
            </section>

            <p className='paragraph content-information'>
                In den weiteren Teilen dieser Website werden nun Einblicke in diese Bereiche gegeben: 
            </p>

            <section id="income">
                <IncomeSection/>
            </section>

            <section id="residualIncome">
                {/* <ResidualIncomeSection/> */}
                <Suspense>
                    <ResidualIncomeSection/>
                </Suspense>
            </section>

            <section id="social-infrastructure">
            <h2 className="section-header">Soziale Infrastruktur in sterreich</h2>
            <p className='paragraph'>
            <GlossaryTerm className="glossary-term si" sectionId="social-infrastructure">Soziale Infrastrukturen</GlossaryTerm> wie Kindergrten, Schulen, Gesundheits- und Pflegeeinrichtungen sind essenziell fr eine funktionierende Gesellschaft. Sie ermglichen Bildung, Gesundheit und soziale Teilhabe  unabhngig vom Einkommen. Doch ihr Zugang und ihre Qualitt sind nicht berall gleich. Regionale Unterschiede und fehlende Angebote knnen Haushalte zustzlich belasten und Ungleichheiten verstrken.
            </p>

            <p className='paragraph'>
                Die Karten zeigen die <GlossaryTerm className="glossary-term si" sectionId="social-infrastructure">sozialen Infrastrukturen</GlossaryTerm> in Bezug auf den Bedarf in der Gemeinde. Whrend rzt:innen pro Einwohner:in berechnet werden, beziehen sich Kindergartengruppen auf Kinder im Alter von 3 bis 6 Jahren und Pltze in Pflegeheimen auf Menschen ber 70. Neben der Verfgbarkeit von Infrastrukturen spielt auch ihre Zugnglichkeit eine Rolle: Kurze ffnungszeiten oder hohe Kosten knnen die Nutzung einschrnken. Da viele Menschen auch die Infrastrukturen in Nachbargemeinden nutzen, werden diese  mit geringerem Gewicht  in den Infrastrukturindikator einbezogen. In der Karte kann dieser Effekt ein- und ausgeblendet werden. Die obere Karte zeigt die Gesamtsumme aller Infrastrukturen, whrend in der unteren Karte die verschiedenen Infrastrukturarten einzeln betrachtet werden knnen. Genauere Informationen zu den einzelnen Infrastrukturen und der Berechnung der Indikatoren lassen sich unten ausklappen.
            </p>

            {/* SI map overall */}
            <Box
                sx={{
                    background: "#f4f4f4",
                    padding: 2,
                    borderRadius: 1,
                    boxShadow: 1,
                    fontSize: '14px',
                    marginTop: 3,
                }}
            >
                <div className="toggle-container">
                    <ToggleButtonGroup
                        className="si-toggle"
                        value={siMode}
                        exclusive
                        onChange={(_, newValue) => handleToggleModeChange("si", newValue)}
                        aria-label="mode selection"
                    >
                    {/* <ToggleButton value="so_cars">PKW</ToggleButton> */}
                    {/* <ToggleButton value="so_miv">MIV</ToggleButton> */}
                    <ToggleButton value="so_cars"><DirectionsCarIcon/></ToggleButton>
                    <ToggleButton value="so_miv"><TrainIcon/></ToggleButton>
                    <ToggleButton value="no_so"><label>No Spillover</label></ToggleButton>
                    </ToggleButtonGroup>
                </div>
        
                <div className="si-overall-container">
                    <SIOverallIndicatorMap siMode={siMode} key={siMode} />
                </div>
            </Box>

            {/* SI map sub indicators */}
            <Box
                sx={{
                    background: "#f4f4f4",
                    padding: 2,
                    borderRadius: 1,
                    boxShadow: 1,
                    fontSize: '14px',
                    margin: '24px 0 48px',
                }}
            >
                <div className="control-container">
                    <div className="select-component">
                        <IndicatorSelect 
                            value={selectedIndicator}
                            onChange={handleIndicatorChange}
                        />
                    </div>

                    <div className="toggle-container">
                        <ToggleButtonGroup
                            className="si-toggle"
                            value={siSubMode}
                            exclusive
                            onChange={(_, newValue) => handleToggleModeChange("si-sub", newValue)}
                            aria-label="mode selection"
                        >
                            <ToggleButton value="so_sub"><label>Spillover</label></ToggleButton>
                            <ToggleButton value="no_so_sub"><label>No Spillover</label></ToggleButton>
                        </ToggleButtonGroup>
                    </div>
                </div>
                
                <div className="si-subindicator-container">
                    <SISubIndicatorMap siMode={siSubMode} subIndicator={selectedIndicator} key={`${siSubMode}-${selectedIndicator}`} />
                </div>
            </Box>

            <p className='paragraph'>
                Die Verfgbarkeit sozialer Infrastruktur wie rzt:innen, Schulen, Kindergrten und sozialen Einrichtungen spielt eine entscheidende Rolle fr das Residualeinkommen von Haushalten. In Regionen, in denen diese Angebote gut ausgebaut sind, profitieren Haushalte von niedrigeren privaten Ausgaben fr Bildung, Gesundheit und soziale Dienstleistungen. Umgekehrt fhrt ein mangelhafter Zugang zu diesen Angeboten dazu, dass Menschen gezwungen sind, private Alternativen zu finanzieren  sei es durch lngere Anfahrtswege, kostenpflichtige Dienstleistungen oder haushaltsinterne Bereitstellung (wie beispielsweise Kinderbetreuung oder Pflege von Angehrigen).
            </p>

            <p className='paragraph'>
                Dies hat direkte Folgen fr das Residualeinkommen: Haushalte in Regionen mit schlechter sozialer Infrastruktur mssen oft hhere Fixkosten tragen, obwohl ihr Einkommen identisch mit dem von Haushalten in besser versorgten Gebieten ist. Besonders betroffen sind Familien mit Kindern, ltere Menschen oder Menschen mit gesundheitlichen Einschrnkungen, da sie strker auf diese Angebote angewiesen sind.
            </p>

            <p className='paragraph'>
            Langfristig verstrkt sich dadurch die soziale Ungleichheit. Haushalte mit ohnehin geringem Einkommen werden durch zustzliche Ausgaben weiter belastet, whrend jene mit besserem Zugang zu Infrastruktur grere finanzielle Spielrume haben. Regionen mit schlechter Infrastruktur verlieren zudem an Attraktivitt, was zu Abwanderung und einem wirtschaftlichen Abwrtstrend fhren knnte.
            </p>

            <h2 className="content-header">Wie werden die einzelnen Infrastrukturindikatoren berechnet?</h2>

            <p className='paragraph'>
                Der Gesamtindikator fr <GlossaryTerm className="glossary-term si" sectionId="social-infrastructure">soziale Infrastrukturen</GlossaryTerm> setzt sich aus sechs gleich gewichteten Teilindikatoren zusammen: Kindergrten, Schulen, Krankenhuser, Allgemeinmediziner:innen, Pflegeeinrichtungen und andere soziale Einrichtungen. Innerhalb der Teilindikatoren wird die Verfgbarkeit mit 50 %, die Zugnglichkeit mit 30 % und die Einflsse von Nachbargemeinden (Spillover-Effekte) mit 20 % gewichtet, um die unterschiedlichen Aspekte der Infrastruktur umfassend zu bercksichtigen.
            </p>

            {/* SI Table*/}
            <SocialInfrastructureTable/>

            </section>

            <section id="time-usage">
            <h2 className="section-header">Zeitverwendung</h2>
                
                <p className='paragraph'>
                In sterreich  wie in vielen anderen Lndern  bernehmen Frauen nach wie vor einen berproportional groen Anteil an unbezahlter Arbeit, darunter Haushaltsfhrung, Kinderbetreuung und die Pflege von Angehrigen. Dies hat weitreichende finanzielle und soziale Konsequenzen.
                </p>

                <div className='paragraph'>
                    <p>
                    Da Frauen mehr Zeit fr unbezahlte Arbeit aufwenden, haben sie oft geringere Erwerbsarbeitszeiten, was zu einem niedrigeren Haushaltseinkommen fhrt. Gleichzeitig beeinflusst dies das Residualeinkommen, denn:
                    </p>
                    <ul className="custom-list">
                        <li>Frauen in Teilzeit oder mit unterbrochenen Erwerbsbiografien haben oft niedrigere Lhne und Sozialleistungen (z. B. Pensionen).</li>
                        <li>Wenn gnstige Kinderbetreuung oder Pflegeeinrichtungen fehlen, sind Haushalte gezwungen, private Lsungen zu finanzieren oder eine Person (oft Frauen) bleibt zu Hause  was das verfgbare Einkommen weiter reduziert.</li>
                        <li>Zeitintensive Care-Arbeit kann dazu fhren, dass Frauen weniger in Weiterbildungen oder Karrierechancen investieren knnen, was langfristig Einkommens- und Vermgensunterschiede verfestigt.</li>
                    </ul>
                </div>

                <p className='paragraph'>
                    Die Verfgbarkeit sozialer Infrastruktur wie Kinderbetreuungseinrichtungen, Ganztagsschulen und Pflegeangebote hat einen direkten Einfluss darauf, wie Erwerbs- und Care-Arbeit innerhalb von Haushalten aufgeteilt wird. In Regionen mit gut ausgebauter Infrastruktur knnen Frauen hufiger Vollzeit oder in besser bezahlten Positionen arbeiten, da die Betreuung von Kindern oder Angehrigen nicht vollstndig auf private Ressourcen angewiesen ist.
                </p>

                <p className='paragraph'>
                    Fehlt diese Infrastruktur, werden Haushalte nicht nur durch direkte Kosten (z. B. private Betreuung), sondern auch durch indirekte Einbuen belastet  etwa durch entgangenes Einkommen oder verringerte Karrieremglichkeiten. Dies wirkt sich langfristig auf das Residualeinkommen und die wirtschaftliche Absicherung von Frauen aus.
                </p>
                
                <h2 className="content-header">Fokus Tageszeitnutzung nach Geschlecht und Alter</h2>

                <p className='paragraph'>
                    Schaut man sich die Zeitverwendung unterschiedlicher Altersklassen an, wird deutlich, dass sich Ungleichheiten bei bezahlter und unbezahlter Arbeit schon frh abzeichnen. Bereits bei den unter 20-Jhrigen verrichten Mdchen und junge Frauen tglich rund 30 Minuten mehr unbezahlte Arbeit als Burschen und junge Mnner. Diese Unterschiede verstrken sich mit zunehmendem Alter.
                </p>

                <p className='paragraph'>
                    In der Altersgruppe 21 bis 64 Jahre wird der Unterschied besonders deutlich: Frauen investieren tglich 3,75 Stunden in unbezahlte Arbeit, whrend Mnner dafr im Schnitt nur 2 Stunden aufwenden. Dazu zhlen Haushaltsaufgaben, Kinderbetreuung und die Pflege von Angehrigen  Ttigkeiten, die essenziell fr die Gesellschaft sind, aber in klassischen Wirtschaftsstatistiken oft nicht sichtbar werden.
                </p>

                <p className='paragraph'>
                    Auch bei den ber 65-Jhrigen bleibt dieser Unterschied bestehen. Whrend Mnner in dieser Altersgruppe ihre unbezahlte Arbeit leicht erhhen, tragen Frauen weiterhin die Hauptlast der hauswirtschaftlichen und pflegerischen Ttigkeiten.
                </p>

                <p className='paragraph'>
                    Wie bereits erwhnt, zeigt sich bei der Erwerbsarbeit das umgekehrte Bild: Mnner arbeiten im Durchschnitt lnger und verdienen dadurch mehr. Das hat weitreichende Konsequenzen wie niedrigere Einkommen von Frauen, was ihr Residualeinkommen und ihre finanzielle Sicherheit langfristig beeintrchtigt. Langfristig wirken sich diese Unterschiede auf Pensionen und Altersarmut aus, da Frauen aufgrund ihrer Erwerbshistorie oft geringere Ansprche haben.
                </p>

                <p className='paragraph'>
                    Diese Dynamik zeigt, dass die ungleiche Verteilung von Zeit fr Erwerbs- und unbezahlte Arbeit ein zentraler Faktor fr wirtschaftliche Ungleichheit ist. In den nchsten Analysen konzentrieren wir uns daher gezielt auf die Altersgruppe 21 bis 64 Jahre, um die Auswirkungen dieser Verteilung noch genauer zu untersuchen.
                </p>

                <p style={{margin: '32px 0 0'}}>TODO: Plot Zeitverwendung nach Altersgruppe</p>


                <h2 className="content-header">Zeitverwendung von Mnnern und Frauen (21-64 Jahre) fr Erwerbsarbeit und unbezahlter Arbeit</h2>
                <h3 className="content-header">Bildung: Bildung verndert viel  aber nicht alles</h3>
                <p className='paragraph'>
                Unabhngig vom Bildungsgrad zeigt sich ein klares Muster: Frauen bernehmen mehr Care-Arbeit als Mnner. Egal ob Pflichtschulabschluss, Lehre oder Universittsabschluss  Frauen verbringen tglich mehr Zeit mit Haushaltsarbeit, Kinderbetreuung und der Pflege von Angehrigen als Mnner.
                </p>
                <p className='paragraph'>
                Whrend sich die Aufteilung zwischen Erwerbsarbeit und Care-Arbeit bei Mnnern kaum verndert, wenn man verschiedene Bildungsgruppen betrachtet, zeigt sich bei Frauen eine leichte Verschiebung: Mit zunehmendem Bildungsgrad investieren Frauen etwas weniger Zeit in unbezahlte Arbeit und etwas mehr in Erwerbsarbeit.
                </p>
                <p className='paragraph'>
                Warum ist das so? Eine mgliche Erklrung liegt in den Arbeitsmarktchancen hher gebildeter Frauen. Frauen mit hherem Bildungsabschluss haben bessere Einkommensperspektiven, was dazu fhren kann, dass sie sich strker in den Arbeitsmarkt einbringen. Zudem besteht in hher gebildeten Haushalten oft eine grere finanzielle Mglichkeit, Dienstleistungen wie Kinderbetreuung oder Haushaltshilfe auszulagern.
                </p>
                <p className='paragraph'>
                Trotz dieser Unterschiede bleibt die Grundtendenz bestehen: Auch hochgebildete Frauen bernehmen mehr unbezahlte Arbeit als Mnner mit gleichem Bildungsniveau. Dies zeigt, dass traditionelle Rollenverteilungen weiterhin Einfluss darauf haben, wie Erwerbs- und Care-Arbeit innerhalb von Haushalten verteilt wird  unabhngig vom Bildungsgrad.
                </p>

                <p style={{margin: '32px 0 0'}}>TODO: 9_INC_SEX_EDU_TU hover effects</p>
                
                <div className="image-container box-plot-container">
                    <img src={`${process.env.PUBLIC_URL}/images/9_INC_SEX_EDU_TU.png`} alt="box plot visualizing income based on sex and education" />
                </div>

                <h3 className="content-header">Vollzeit-/Teilzeitarbeit: Teilzeit ist nicht gleich Teilzeit  die doppelte Belastung von Frauen</h3>
                <p className='paragraph'>
                Frauen, die Teilzeit arbeiten, bernehmen fast doppelt so viel Care-Arbeit wie Frauen, die Vollzeit arbeiten. Dies knnte darauf zurckzufhren sein, dass viele Frauen ihre Arbeitszeit bewusst reduzieren, um Haushalt, Kinderbetreuung oder die Pflege von Angehrigen zu bernehmen. Teilzeit ist in diesem Fall oft kein freiwilliges Modell fr mehr Freizeit, sondern eine Notwendigkeit, um unbezahlte Arbeit im Haushalt zu bewltigen.
                </p>
                <p className='paragraph'>
                Besonders auffllig ist der Unterschied zwischen teilzeitbeschftigten Frauen und Mnnern: Frauen in Teilzeit bernehmen mehr als doppelt so viel Care-Arbeit wie Mnner in Teilzeit. Whrend Mnner, die ihre Arbeitszeit reduzieren, oft mehr persnliche Freizeit oder Erholung gewinnen, nutzen Frauen die gewonnene Zeit meist fr unbezahlte Ttigkeiten.
                </p>
                <p className='paragraph'>
                Das hat weitreichende Folgen: Teilzeit verringert das Erwerbseinkommen, die Karrierechancen und langfristig auch die Pensionsansprche. Gleichzeitig bleibt der Groteil der unbezahlten Arbeit an Frauen hngen, was die finanzielle Abhngigkeit innerhalb von Haushalten verstrken kann. Diese Verteilung zeigt, dass Teilzeitarbeit fr Frauen oft nicht nur eine berufliche Entscheidung ist, sondern direkt mit ungleicher Care-Arbeit verknpft ist  mit langfristigen Konsequenzen fr Einkommen, soziale Absicherung und wirtschaftliche Unabhngigkeit.
                </p>
                
                <p style={{margin: '32px 0 0'}}>TODO: 10_INC_SEX_FTPT_TU hover effects</p>
                
                <div className="image-container ftpt-box-plot-container">
                    <img src={`${process.env.PUBLIC_URL}/images/10_INC_SEX_FTPT_TU.png`} alt="box plot visualizing income based on sex and extent employment" />
                </div>


                <h3 className="content-header">Haushaltstyp: Wenn aus einem Haushalt eine Familie wird  wie sich Arbeitsteilung verndert</h3>
                <p className='paragraph'>
                Bei Single-Frauen und -Mnnern gibt es kaum Unterschiede in der Aufteilung zwischen Erwerbsarbeit und unbezahlter Care-Arbeit. Beide verbringen hnlich viel Zeit mit Haushaltsttigkeiten und gehen einer Erwerbsarbeit nach. Doch sobald Menschen in Paarhaushalten leben, beginnt sich die Verteilung zu verndern: Frauen bernehmen im Durchschnitt 50 Minuten mehr unbezahlte Arbeit pro Tag als ihre mnnlichen Partner.
                </p>
                <p className='paragraph'>
                Besonders deutlich wird die Ungleichverteilung, sobald Kinder ins Spiel kommen. In Paarhaushalten mit einem Kind unter 15 Jahren verbringen Frauen tglich ber 4 Stunden mit Care-Arbeit, whrend Mnner weniger als 2 Stunden fr Haushalt und Kinderbetreuung aufwenden. Dies geht mit einer gegenlufigen Entwicklung in der Erwerbsarbeit einher: Mnner arbeiten in diesem Haushaltstyp fast 2 Stunden mehr pro Tag als Frauen.
                </p>
                <p className='paragraph'>
                Je mehr Kinder im Haushalt leben, desto strker verstrkt sich dieses Muster. Frauen reduzieren ihre Erwerbsarbeitszeit weiter, um mehr Zeit fr Haushalt und Kinder aufzuwenden, whrend Mnner ihre Erwerbsarbeit sogar noch ausweiten. Diese Entwicklung zeigt, dass traditionelle Rollenmuster in Familien trotz gesellschaftlichem Wandel weiterhin tief verankert sind. Auch bei Alleinerziehenden bleibt dieser Unterschied bestehen: Alleinerziehende Mnner arbeiten im Durchschnitt mehr, whrend alleinerziehende Frauen mehr Care-Arbeit leisten. 
                </p>
                <p className='paragraph'>
                Um diese Ungleichheiten zu reduzieren, sind gezielte Manahmen erforderlich: Der Ausbau von Kinderbetreuung, Pflegeangeboten und flexibleren Arbeitsmodellen kann Frauen und Mnnern mehr Spielraum bei der Vereinbarkeit von Beruf und Familie geben. Zudem sind gerechtere Verteilungen von Erwerbs- und Care-Arbeit nicht nur eine Frage individueller Entscheidungen, sondern auch von gesellschaftlichen Strukturen und politischen Rahmenbedingungen.
                </p>

                <p style={{margin: '32px 0 0'}}>TODO: 11_INC_SEX_HHTYPE_TU hover effects</p>
                
                <div className="image-container box-plot-container">
                    <img src={`${process.env.PUBLIC_URL}/images/11_INC_SEX_HHTYPE_TU.png`} alt="box plot visualizing income based on sex and household type" />
                </div>

                {/* Check if h2 or h3 */}
                <h3 className="content-header">Gemeindegreklasse: Stadt oder Land? Wie der Wohnort die Arbeitsteilung beeinflusst</h3>
                <p className='paragraph'>
                Schaut man sich die Zeitverwendung in unterschiedlichen Gemeindegren an, zeigt sich ein klares Muster: Frauen in den lndlichsten Gemeinden leisten tglich mehr als eine Stunde mehr Care-Arbeit als Frauen in Wien. Je grer die Gemeinde, desto geringer wird der Anteil an unbezahlter Arbeit, den Frauen bernehmen.
                </p>
                <p className='paragraph'>
                Bei Mnnern hingegen bleibt die Verteilung nahezu unverndert  egal, ob sie in einer kleinen Landgemeinde oder in einer Grostadt leben. Whrend Frauen auf dem Land also mehr Zeit fr Haushalt, Kinderbetreuung und Pflege aufwenden, verndert sich der Anteil der mnnlichen Care-Arbeit kaum.
                </p>

                <p className='paragraph'>
                Dies zeigt, dass lndliche Regionen weniger Infrastruktur fr untersttzende Dienstleistungen bieten, wodurch mehr Care-Arbeit privat organisiert werden muss. Weniger verfgbare Kinderbetreuungspltze, lngere Wege zu rzt:innen oder soziale Erwartungen in kleinen Gemeinden knnen dazu fhren, dass Frauen auf dem Land mehr unbezahlte Arbeit bernehmen und dadurch oft weniger Erwerbsarbeit leisten knnen.
                </p>

                <p style={{margin: '32px 0 0'}}>TODO: 12_INC_SEX_GEMGRO_TU hover effects</p>
                
                <div className="image-container box-plot-container">
                    <img src={`${process.env.PUBLIC_URL}/images/12_INC_SEX_GEMGRO_TU.png`} alt="box plot visualizing income based on sex and community size" />
                </div>

                <div className='paragraph' style={{margin: '32px 0 0'}}>
                    <p>
                    Zusammenhang zwischen der Erreichbarkeit sozialer Infrastruktur (z. B. Kinderbetreuung, Gesundheitsversorgung) und Zeitverwendung.
                    </p>
                    <ul className="custom-list">
                        <li>Wie beeinflussen lange Pendelzeiten oder weite Wege zur Infrastruktur die tgliche Zeitaufteilung?</li>
                        <li>Haben Regionen mit besser ausgebauter Infrastruktur geringere Zeitbelastungen fr Hausarbeit und Mobilitt?</li>
                    </ul>
                </div>


            </section>

            <section id="what-now">
                <h2 className="section-header">Was nun?</h2>

                <p className='paragraph'>
                    Ungleichheit ist mehr als nur eine Frage des Einkommens. Wer wie viel verdient, bestimmt zwar den monetren Spielraum, doch erst im Zusammenspiel mit notwendigen Ausgaben, aufgewandter Zeit (vor allem fr Erwerbsarbeit und unbezahlter Arbeit) und ffentlicher sozialer Infrastruktur entsteht ein breiteres Bild sozialer Ungleichheit. Unser Projekt zeigt: Diese Faktoren sind nicht isoliert zu betrachten. Sie verstrken sich gegenseitig und beeinflussen. Um nachhaltige Lsungen zu entwickeln, muss Politik diese Zusammenhnge anerkennen und gezielt eingreifen.
                </p>

                <div className='outlook-chart'>
                    <OutlookChart/>
                </div>
            </section>

        </div>

        <div className="floatingButton-container"> 
            <FloatingButton show={showFloatingButton} />
        </div>

        <div className='progressBar'>
            <ScrollProgressBar/>
        </div>
        </>
    );
}